.btn {
  font-family: 'Gotham Pro';
  color: white;
  background: linear-gradient(to right, #ffe100 0%, #f29c0b 55%, #ff7d00 100%);
  border: none;
  border-radius: 7px;
  padding: 10px 12px;
  margin: 0 auto;
  font-weight: 700;
  font-size: 12px;
}

@media (min-width: 768px) {
  .btn {
    font-size: 14px;
  }
}

@media (min-width: 1024px) {
  .btn {
    font-size: 16px;
  }
}
