body {
  margin: 0;
  font-family: 'Gotham Pro', 'Adobe Invis Font', 'Myriad Pro', -apple-system,
    BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url('./img/main-background.jpg') top center no-repeat,
    url('./img/footer-background.jpg') bottom center no-repeat;
  background-size: 100vw 40vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

#root {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
  display: flex;
  flex-direction: column;
  padding: 0 10px 0 10px;
  background: url('./img/personList-background.png') left 14% no-repeat,
    url('./img/personList-background2.png') right 14% no-repeat,
    url('./img/couple.png') calc(100% - 5px) 59.6% no-repeat,
    url('./img/teachers-background.png') right 79% no-repeat;
}

a {
  font-family: 'Gotham Pro', 'Adobe Invis Font', 'Myriad Pro', -apple-system,
    BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  text-decoration: none;
  color: #232323;

  transition: color 0.3s ease;
}

a:hover {
  color: tomato;
}

#menuToggle {
  display: block;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;

  z-index: 5;

  -webkit-user-select: none;
  user-select: none;
}

#menuToggle input {
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;
  top: -7px;
  left: -5px;
  cursor: pointer;
  opacity: 0;
  z-index: 6;
  -webkit-touch-callout: none;
}

#menuToggle span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #fff;
  border-radius: 3px;
  z-index: 5;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}

#menuToggle span:first-child {
  transform-origin: 0% 0%;
}

#menuToggle span:nth-last-child(2) {
  transform-origin: 0% 100%;
}

#menuToggle input:checked ~ span {
  opacity: 1;
  transform: rotate(45deg) translate(-2px, -1px);
  background: #232323;
  will-change: transform;
}

#menuToggle input:checked ~ span:nth-last-child(3) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
  will-change: transform;
}

#menuToggle input:checked ~ span:nth-last-child(2) {
  transform: rotate(-45deg) translate(0, -1px);
  will-change: transform;
}

#menu {
  position: absolute;
  top: -10vw;
  right: -8px;
  width: 100vw;
  padding: 50px;
  background: #ededed;
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  transform-origin: 0% 0%;
  transform: translate(100%, 0);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  will-change: transform;
}

#menu li {
  padding: 10px 0;
  font-size: 22px;
  z-index: 4;
}

#menuToggle input:checked ~ ul {
  transform: none;
  z-index: 4;
}

#defaultNav {
  width: 80%;
}

#defaultMenu {
  max-width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  list-style: none;
}

#defaultMenu li {
  font-size: 15px;
  font-weight: 600;
  color: #fff;
}

@media (min-width: 768px) {
  body {
    -moz-osx-font-smoothing: grayscale;
    background: url('./img/header.png') top center no-repeat,
      url('./img/footer.png') bottom center no-repeat;
    background-size: 100vw auto;
  }

  main {
    background: url('./img/personList-background.png') left 18% no-repeat,
      url('./img/personList-background2.png') right 18% no-repeat,
      url('./img/couple.png') calc(100% + 10px) 52% no-repeat,
      url('./img/teachers-background.png') right 72% no-repeat;
  }

  #defaultNav {
    min-width: 600px;
    max-width: 780px;
    width: 60%;
  }

  #defaultMenu {
    max-width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
  }

  #defaultMenu li {
    font-size: 18px;
    font-weight: 500;
  }

  #defaultMenu a {
    margin: 0 auto;
  }

  a {
    color: #fff;
  }
}

@media (min-width: 1024px) {
  * {
    -webkit-overflow-scrolling: auto;
  }

  #root {
    overflow-y: auto;
    -webkit-overflow-scrolling: auto;
  }

  body {
    overflow-y: auto;
  }

  body::-webkit-scrollbar {
    width: 0.7em;
  }

  body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.35);
  }

  body::-webkit-scrollbar-thumb {
    background-color: #f48b1e;
  }

  body::-webkit-scrollbar-thumb:hover {
    background-color: #e18616;
  }

  body::-webkit-scrollbar-thumb:active {
    background-color: #f04423;
  }

  #defaultNav {
    min-width: 600px;
    max-width: 780px;
    width: 60%;
  }

  #defaultMenu {
    max-width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
  }

  #defaultMenu li {
    font-size: 18px;
    font-weight: 500;
  }

  #defaultMenu a {
    margin: 0 auto;
  }

  body {
    -moz-osx-font-smoothing: grayscale;
    background: url('./img/header.png') top center no-repeat,
      url('./img/footer.png') bottom center no-repeat;
    /* url(./img/footer-bg.png) left 55% no-repeat; */
    background-size: 100vw auto;
  }

  main {
    background: url('./img/personList-background.png') left 25% no-repeat,
      url('./img/personList-background2.png') right 25% no-repeat,
      url('./img/left-background.png') left 53% no-repeat,
      url('./img/right-background.png') right 53% no-repeat,
      url('./img/teachers-backgroundLaptop.png') right 87% no-repeat;
  }
}
