.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5px auto;
}

.input {
  width: 100%;
  max-width: 290px;
  padding: 8px 15px;
  margin: 12px 0;
  border-radius: 4px;
  border: 1px solid #000;
}

.input::placeholder {
  font-family: 'Gotham Pro';
  letter-spacing: 1px;
}
